import { HttpClient, HttpParams } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { map } from 'rxjs/operators';
import { environment } from 'src/environments/environment';
import {
  ApplicantParams,
  IApplicantEditor,
  IApplicant,
} from '../shared/models/applicant';
import { Ilookup } from '../shared/models/lookup';
import { IPagination, Pagination } from '../shared/models/pagination';

@Injectable({
  providedIn: 'root',
})
export class ApplicantService {
  baseUrl = environment.apiUrl;
  pagination = new Pagination();
  countries: Ilookup[] = [];

  applicant: IApplicantEditor;
  applicantLookup: IApplicant;
  constructor(private http: HttpClient) {}

  getApplicantById(id: number) {
    return this.http.get<IApplicant>(this.baseUrl + `applicant/` + id);
  }
  getApplicants(applicantParams: ApplicantParams) {
    let params = new HttpParams();

    if (applicantParams.search) {
      params = params.append('search', applicantParams.search);
    }

    params = params.append('sort', applicantParams.sort);
    params = params.append('sortDirection', applicantParams.sortDirection);
    params = params.append('pageIndex', applicantParams.pageNumber.toString());
    params = params.append('pageSize', applicantParams.pageSize.toString());

    return this.http
      .get<IPagination>(this.baseUrl + `applicant`, {
        observe: 'response',
        params,
      })
      .pipe(
        map((response) => {
          this.pagination = response.body;
          return this.pagination;
        })
      );
  }
  exportApplicants(applicantParams: ApplicantParams) {
    let params = new HttpParams();

    params = params.append('pageIndex', applicantParams.pageNumber.toString());
    params = params.append('pageSize', applicantParams.pageSize.toString());
    return this.http
      .get(this.baseUrl + `applicant/export`, {
        responseType: 'blob',
        params: params,
      })
      .pipe();
  }
  saveApplicant(values: IApplicantEditor, id: number) {
    return this.http
      .post<IApplicantEditor>(
        this.baseUrl + `applicant/${id > 0 ? 'update' : 'create'}`,
        values
      )
      .pipe(
        map((response) => {
          this.applicant = response;
          return this.applicant;
        })
      );
  }
  getCountriesLookup() {
    return this.http
      .get<Ilookup[]>(this.baseUrl + 'helper/countrylookup', {
        observe: 'response',
      })
      .pipe(
        map((response) => {
          this.countries = response.body;
          return this.countries;
        })
      );
  }

  deleteApplicant(id: number) {
    return this.http.delete<IApplicant>(this.baseUrl + `applicant/${id}`).pipe(
      map((response) => {
        this.applicant = response;
        return this.applicant;
      })
    );
  }

  applicantExist(name: string, familyName: string, email: string, id: number) {
    let params = new HttpParams();
    params = params.append('name', name);
    params = params.append('familyName', familyName);
    params = params.append('email', email);
    params = params.append('id', id.toString());
    return this.http
      .get<boolean>(this.baseUrl + `applicant/applicantexists`, {
        observe: 'response',
        params,
      })
      .pipe(
        map((response) => {
          return response;
        })
      );
  }
}
