import {
  Component,
  ElementRef,
  OnInit,
  TemplateRef,
  ViewChild,
} from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import * as FileSaver from 'file-saver';
import { BsModalRef, BsModalService } from 'ngx-bootstrap/modal';
import { ToastrService } from 'ngx-toastr';
import { ApplicantParams, IApplicant } from '../shared/models/applicant';
import { Ilookup } from '../shared/models/lookup';
import { ApplicantService } from './applicant.service';

@Component({
  selector: 'app-applicant',
  templateUrl: './applicant.component.html',
  styleUrls: ['./applicant.component.scss'],
})
export class ApplicantComponent implements OnInit {
  countries: Ilookup[];

  public totalItems = 64;
  @ViewChild('search', { static: false }) searchTerm: ElementRef;
  @ViewChild('fromDate', { static: false }) fromDateTerm: ElementRef;
  @ViewChild('toDate', { static: false }) toDateTerm: ElementRef;
  @ViewChild('invoiceFromDate', { static: false }) invoiceFromDate: ElementRef;
  @ViewChild('invoiceToDate', { static: false }) invoiceToDate: ElementRef;

  applicants: IApplicant[] = [];
  applicantParams = new ApplicantParams();
  exportParams = new ApplicantParams();
  totalCount: number;
  editormodalRef: BsModalRef;
  ApplicantId: number = 0;
  deletemodalRef: BsModalRef;
  constructor(
    private applicantService: ApplicantService,
    private activatedroute: ActivatedRoute,
    private toastr: ToastrService,
    private router: Router,
    private modalService: BsModalService
  ) {}

  ngOnInit(): void {
    this.getApplicants();
  }

  getApplicants() {
    this.applicantService.getApplicants(this.applicantParams).subscribe(
      (res) => {
        this.applicants = res.data;
        console.log(this.applicants);
        this.applicantParams.pageNumber = res.pageIndex;
        this.applicantParams.pageSize = res.pageSize;
        this.totalCount = res.count;
      },
      (error) => {
        console.log(error);
      }
    );
  }

  onPageChanged(event: any) {
    if (this.applicantParams.pageNumber !== event) {
      this.applicantParams.pageNumber = event;
      this.getApplicants();
    }
  }

  onSearch() {
    this.applicantParams.search = this.searchTerm.nativeElement.value;
    this.applicantParams.pageNumber = 1;
    this.getApplicants();
  }

  Export() {
    this.exportParams.pageNumber = 1;
    this.exportParams.pageSize = 1000000;

    this.applicantService.exportApplicants(this.exportParams).subscribe(
      (data: Blob) => {
        var fileName = 'Applicants_list_' + Date.now() + '';
        FileSaver.saveAs(data, fileName);
      },
      (err: any) => {
        console.log(`Unable to export file`);
      }
    );
  }
  openEditorModal(template: TemplateRef<any>, id: number) {
    this.ApplicantId = id;
    this.editormodalRef = this.modalService.show(template, {
      backdrop: 'static',
    });
  }
  openDeleteModal(template: TemplateRef<any>) {
    this.deletemodalRef = this.modalService.show(template);
  }

  deleteApplicant(id: number) {
    this.applicantService.deleteApplicant(id).subscribe(
      () => {
        this.deletemodalRef.hide();
        this.toastr.success(`Applicant has been deleted successfully`);
        setTimeout(() => {
          this.reloadComponent();
        }, 2000);
      },
      (err) => {
        console.log(err);
      }
    );
  }

  reloadCurrentPage() {
    let currentUrl = this.router.url;
    this.router.navigateByUrl('/', { skipLocationChange: true }).then(() => {
      this.router.navigate([currentUrl]);
    });
  }
  reloadComponent() {
    let currentUrl = this.router.url;
    this.router.routeReuseStrategy.shouldReuseRoute = () => false;
    this.router.onSameUrlNavigation = 'reload';
    this.router.navigate([currentUrl]);
  }
}
