import { Component, Input, OnInit } from '@angular/core';
import {
  FormBuilder,
  FormControl,
  FormGroup,
  Validators,
} from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { BsModalRef } from 'ngx-bootstrap/modal';
import { ToastrService } from 'ngx-toastr';
import { IApplicant, IApplicantEditor } from 'src/app/shared/models/applicant';
import { Ilookup } from 'src/app/shared/models/lookup';
import { ApplicantService } from '../applicant.service';

@Component({
  selector: 'app-Applicant-editor',
  templateUrl: './Applicant-editor.component.html',
  styleUrls: ['./Applicant-editor.component.scss'],
})
export class ApplicantEditorComponent implements OnInit {
  ApplicantForm: FormGroup;
  ApplicantEditor: IApplicantEditor;
  Applicant: IApplicant;
  @Input() ApplicantId: number;
  @Input() isEdit: boolean;

  @Input() editormodalRef: BsModalRef;
  countries: Ilookup[];

  constructor(
    private applicantService: ApplicantService,
    private router: Router,
    private formBuilder: FormBuilder,
    private toastr: ToastrService,
    private activatedroute: ActivatedRoute
  ) {}

  ngOnInit(): void {
    this.createApplicantForm();
    this.getCountriesLookup();
  }

  createApplicantForm() {
    this.ApplicantForm = this.formBuilder.group({
      id: new FormControl(0),
      name: new FormControl('', [Validators.required, Validators.minLength(5)]),
      familyName: new FormControl('', [
        Validators.required,
        Validators.minLength(5),
      ]),
      address: new FormControl('', [
        Validators.required,
        Validators.minLength(10),
      ]),
      countryOfOrigin: new FormControl('', [Validators.required]),
      eMailAdress: new FormControl('', [
        Validators.required,
        Validators.pattern('^[\\w-\\.]+@([\\w-]+\\.)+[\\w-]{2,4}$'),
      ]),
      age: new FormControl('', [
        Validators.required,
        Validators.min(20),
        Validators.max(60),
      ]),
      hired: new FormControl(false, [Validators.required]),
    });
  }

  collectFormData() {
    const model: IApplicantEditor = {
      id: this.ApplicantForm.controls['id'].value,
      name: this.ApplicantForm.controls['name'].value,
      familyName: this.ApplicantForm.controls['familyName'].value,
      address: this.ApplicantForm.controls['address'].value,
      countryOfOrigin: this.ApplicantForm.controls['countryOfOrigin'].value,
      eMailAdress: this.ApplicantForm.controls['eMailAdress'].value,
      age: this.ApplicantForm.controls['age'].value,
      hired: this.ApplicantForm.controls['hired'].value,
    };
    this.ApplicantEditor = model;
  }
  assignFormData(Applicant: IApplicant) {
    this.ApplicantForm.controls['id'].setValue(Applicant.id);
    this.ApplicantForm.controls['name'].setValue(Applicant.name);
    this.ApplicantForm.controls['familyName'].setValue(Applicant.familyName);
    this.ApplicantForm.controls['address'].setValue(Applicant.address);

    this.ApplicantForm.controls['countryOfOrigin'].setValue(
      Applicant.countryOfOrigin
    );
    this.ApplicantForm.controls['eMailAdress'].setValue(Applicant.eMailAdress);
    this.ApplicantForm.controls['age'].setValue(Applicant.age);
    this.ApplicantForm.controls['hired'].setValue(Applicant.hired);
  }
  loadApplicant() {
    this.applicantService.getApplicantById(this.ApplicantId).subscribe(
      (res) => {
        this.Applicant = res;
        if (this.Applicant) this.assignFormData(this.Applicant);
      },
      (err) => {
        console.log(err.message);
      }
    );
  }

  onSubmit() {
    this.markFormGroupTouched(this.ApplicantForm);
    if (this.ApplicantForm.valid) {
      this.applicantService
        .applicantExist(
          this.ApplicantForm.controls['name'].value,
          this.ApplicantForm.controls['familyName'].value,
          this.ApplicantForm.controls['eMailAdress'].value,
          this.ApplicantForm.controls['id'].value
        )
        .subscribe(
          (res) => {
            if (!res.body) {
              this.collectFormData();
              this.saveApplicant();
            } else {
              this.toastr.error(`Applicant is already exist`);
            }
          },
          (err) => {
            console.log(err.message);
          }
        );
    }
  }

  saveApplicant() {
    this.applicantService
      .saveApplicant(this.ApplicantEditor, this.ApplicantId)
      .subscribe(
        (res) => {
          this.editormodalRef.hide();
          this.toastr.success(`Applicant saved successfully`);

          setTimeout(() => {
            this.reloadComponent();
          }, 2000);
        },
        (err) => {
          console.log(err);
        }
      );
  }
  reloadCurrentPage() {
    debugger;
    let currentUrl = this.router.url;
    this.router.navigateByUrl('/', { skipLocationChange: true }).then(() => {
      this.router.navigate([currentUrl]);
    });
  }
  reloadComponent() {
    let currentUrl = this.router.url;
    this.router.routeReuseStrategy.shouldReuseRoute = () => false;
    this.router.onSameUrlNavigation = 'reload';
    this.router.navigate([currentUrl]);
  }

  getCountriesLookup() {
    this.applicantService.getCountriesLookup().subscribe(
      (res) => {
        this.countries = res;
        debugger;
        if (this.ApplicantId > 0) {
          this.loadApplicant();
        }
      },
      (error) => {
        console.log(error);
      }
    );
  }

  private markFormGroupTouched(formGroup: FormGroup) {
    (<any>Object).values(formGroup.controls).forEach((control) => {
      control.markAsTouched();

      if (control.controls) {
        this.markFormGroupTouched(control);
      }
    });
  }
}
